import * as React from 'react';
import { AppBar, Box, Typography, useMediaQuery, Link, Divider } from '@mui/material';
import CheckCircleOutlineTwoToneIcon from '@mui/icons-material/CheckCircleOutlineTwoTone';
import SmartphoneIcon from '@mui/icons-material/Smartphone';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';

import logo from '../image/logo.jpeg';

const list = [
  'Оригинальные материалы от производителя потолков',
  'Гарантия 10 лет на все комплектующие и работу',
  'Быстрый монтаж - производство и монтаж в течении 1-3 дней',
  'Бесплатный выезд замерщика и расчета работ',
];

const NavBar = () => {
  const isNonSmallScreens = useMediaQuery('(min-width: 600px)');
  const isNonMediumScreens = useMediaQuery('(min-width: 900px)');

  return (
    <AppBar
      sx={{
        color: 'white',
        position: 'relative',
        minHeight: isNonMediumScreens ? '65vh' : '100vh',
        backgroundImage: `url(${logo})`,
        backgroundSize: 'cover',
        backgroundAttachment: 'fixed',
        backgroundRepeat: 'no-repeat',
        alignItems: 'center',
      }}>
      <Typography
        variant="h1"
        fontSize={isNonSmallScreens ? undefined : '25px'}
        sx={{ zIndex: '2', pt: 3, pb: isNonMediumScreens ? 7 : 10 }}>
        НАТЯЖНЫЕ ПОТОЛКИ
      </Typography>

      <Box sx={{ zIndex: '2', flexGrow: '1' }}>
        <Box
          gap={isNonMediumScreens ? '1rem' : '0.5rem'}
          sx={{ display: 'flex', flexDirection: 'column' }}>
          <Typography
            variant="h2"
            fontSize={isNonSmallScreens ? undefined : '20px'}
            sx={{ zIndex: '2', pb: isNonMediumScreens ? 0 : 0, textAlign: 'center' }}>
            Натяжные потолки по всей Латвии
          </Typography>
          <Divider
            sx={{
              borderColor: '#ffd166',
              zIndex: 3,
              width: isNonSmallScreens ? '100%' : '90%',
              alignSelf: 'center',
            }}
          />
          {list.map((item) => (
            <Typography
              key={item}
              fontSize={isNonSmallScreens ? '20px' : '14px'}
              sx={{ pl: 3, pr: 3 }}>
              <CheckCircleOutlineTwoToneIcon
                fontSize={isNonSmallScreens ? 'large' : 'medium'}
                sx={{
                  position: 'relative',
                  top: isNonSmallScreens ? '7.5px' : '5px',
                  right: '5px',
                  color: '#ffd166',
                }}
              />
              {item}
            </Typography>
          ))}
        </Box>
      </Box>
      <Link
        href="tel:29926325"
        sx={{
          fontFamily: 'Raleway',
          position: isNonMediumScreens ? 'absolute' : undefined,
          top: '20px',
          right: '20px',
          fontSize: '25px',
          color: '#fff',
          zIndex: 3,
          textDecoration: 'none',
          pt: isNonMediumScreens ? undefined : 5,
          pb: isNonMediumScreens ? undefined : 3,

          // cursor: 'pointer',
        }}>
        <SmartphoneIcon
          fontSize="large"
          sx={{ position: 'relative', top: ' 7px', right: '6px', color: '#ffd166' }}
        />
        +371 29926325
      </Link>
      <Box
        sx={{
          position: 'absolute',
          top: '0',
          left: '0',
          width: '100%',
          height: '100%',
          backgroundColor: 'rgba(0,0,0, .35)',
        }}></Box>
      {isNonMediumScreens ? undefined : (
        <ArrowDownwardIcon
          fontSize="large"
          sx={{
            color: '#ffd166',
            zIndex: 2,
            position: 'relative',
            bottom: '10px',
          }}
        />
      )}
    </AppBar>
  );
};

export default NavBar;
